@import url(https://fonts.googleapis.com/css?family=Montserrat|Open+Sans:300,400);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
    text-decoration: none;
    transition: color 0.25s ease;
}

a:hover {
    color: #5d73aa;
}

body {
    margin: 0;
}

.button {
    padding: 10px 20px;
    margin: 0 30px;
    font-family: montserrat;
    color: #e2e2e2;
    font-size: 14px;
    text-decoration: none;
    text-transform: uppercase;
    border: solid 2px #e2e2e2;
    border-radius: 25px;
    transition: opacity 0.25s ease;
}

.button:hover {
    opacity: 0.5;
    color: #e2e2e2;
}

.left {
    text-align: left !important;
}

.light {
    color: #e2e2e2 !important;
}

.red {
    color: #5d73aa !important;
}

@media only screen and (max-width: 767px) {
    .button {
        font-size: 12px;
        margin-bottom: 10px;
    }
}

#nav_bar {
    display: flex;
    justify-content: space-between;
    align-content: center;
    align-self: stretch;
}

#nav_logo_div {
    height: 90px;
    padding-top: 10px;
    text-align: center;
}

#nav_logo {
    height: 70px;
}

#nav_list_items_div {
    display: flex;
    justify-content: center;
    align-items: center;
}

#nav_list {
    display: flex;
    list-style: none;
    padding: 0;
    align-content: center;
    justify-content: center;
}

.nav_list_item {
    padding: 0 10px;
}

.nav_list_item a {
    text-decoration: none;
    color: #e2e2e2;
    font-family: Montserrat;
    text-transform: uppercase;
    transition: color 0.25s ease;
}

.nav_list_item a:hover {
    color: #5d73aa;
}

@media only screen and (max-width: 991px) {
    #nav_bar {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-content: center;
        align-self: stretch;
    }
}
#hero_div {
    padding: 0 15% 60px 15%;
    min-height: 70vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    background: linear-gradient(rgba(93, 115, 170, 0.5),rgba(151, 181, 155, 0.3)), url(/assets/img/background.png);
    background-repeat: no-repeat;
    background-position: center top;
    background-size: auto 80%;
    background-attachment: fixed;
}


#hero_text_div {
    padding: 30px 0;
}

#hero_text_div h1 {
    margin: 0;
    padding: 0 0 15px 0;
    font-family: open sans;
    font-weight: 300;
    color: #e2e2e2;
    font-size: 50px;
    line-height: 50px;
    text-align: center;
}

#hero_text_div p {
    font-family: open sans;
    color: #e2e2e2;
    font-size: 16px;
    font-weight: 300;
    text-align: center;
    padding: 0;
    margin: 0;
}

#buttons_div {
    display:flex;
    justify-content: space-between;
    align-items: center;
}

.button_main {
    background-color: #5d73aa;
    border: solid 2px #5d73aa;
}

@media only screen and (max-width: 767px) {
    #hero_text_div h1 {
        font-size: 28px;
        line-height: 30px;
    }
    
    #hero_div {
        padding: 0 5% 60px 5%;
    }

    #buttons_div {
        flex-direction: column;
    }
}

#showcase_div {
    padding: 100px 15%;
    margin: 0;
    border-top: 5px solid #5d73aa;
}

.div_heading {
    margin: 20px 0;
    padding: 0;
    text-align: center;
    font-family: open sans;
    font-weight: 300;
    color: #525252;
    font-size: 42px;
    letter-spacing: 1px;
}

#showcase_list_div {
    display: flex;
    flex-wrap:wrap;
    padding: 0 5%;
    justify-content: flex-start;
    align-items: stretch;
}

.showcase_item_div {
    padding: 0 30px;
    margin: 30px 0;
    display: flex;
    align-items: center;
    width: 40%;
}

.showcase_image_div {
    width: 100px;
    height: 100px;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid #5d73aa;
    border-radius: 100%;
}

.showcase_text_div {
    align-self: flex-start;
    padding: 0 0 0 20px;
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: flex-start;
}

.showcase_text_div h1 {
    padding: 0;
    margin: 0 0 10px 0;
    font-family: open sans;
    font-weight: 300;
    color: #525252;
    font-size: 28px;
    text-align: center;
}

.div_subheading {
    color: #525252;
    font-family: open sans;
    font-size: 14px;
    text-align: center;
    padding: 0 0 15px 0;
    text-decoration: none;
}

.div_paragraph {
    font-family: open sans;
    font-size: 18px;
    text-align: left;
    padding: 0 0 18px 0;
    text-decoration: none;
    line-height: 1.75;
}

.showcase_image_div img {
    width: 60px;
    height: auto;
}

@media only screen and (max-width: 991px) {

    #showcase_list_div {
        flex-direction: column;
    }
    
    .showcase_item_div {
        width: auto;
        flex-direction: column;
        padding: 0;
        justify-content: center;
        align-items: center;
    }
    
    .showcase_text_div {
        padding: 20px 0 0 0;
    }
    
    .showcase_text_div p {
        padding: 0 25%;
    }
    

}

@media only screen and (max-width: 767px) {

    #showcase_div {
        padding: 50px 5%;
    }
    
    .showcase_text_div p {
        padding: 0;
    }

    .div_heading {
        font-size: 28px;
        padding: 0;
    }
    
    .div_subheading {
        padding: 0 0 20px 0;
        margin: 0;
    }
}
#programs_div {
    padding: 100px 15%;
    background-color: #5d73aa;
    margin: 0;
}

#programs_list_div {
    display: flex;
    align-items: flex-start;
    justify-content: center;
}

.programs_item_div {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 0 30px;
}

.programs_item_div img {
    max-width: 100%;
}


@media only screen and (max-width: 767px) {

    #programs_list_div {
        flex-direction: column;
    }
    
    .programs_item_div {
        padding: 20px 0;
    }

    #programs_div {
        padding: 50px 5%;
    }
    
}
#footer_column_div {
    display: flex;
   padding: 50px 15%;
}

.footer_column {
   width: 33%;
   padding: 0 50px;
}

.footer_column ul {
   list-style: none;
   padding: 0;
}

.footer_column ul li {
   padding: 10px 0;
   border-top: 1px solid #c4c4c4;
}

.footer_column ul li:first-child {
   border: none;
}

.footer {
   color: #888;
   font-size: 26px;
}

.footer_sub {
    color: #888;
}

@media only screen and (max-width: 991px) {
    #footer_column_div {
        flex-direction: column;
    }
    
    .footer_column {
        width: auto;
    }
    
    .footer_column p {
        padding-left: 5%;
    }
    
    .footer {
        text-align: center;
    }
    
    .footer_column ul li {
        text-align: center;
    }


}

@media only screen and (max-width: 767px) {
    #footer_div {
        padding: 50px 5%;
    }
    
    #footer_column_div {
        padding: 0;
    }
    
    .footer_column {
        padding: 20px 0 0 0;
    }
}
